.fondo {
  margin: 0 auto;
  position: absolute;
  top: 0;
  z-index: -2;
  height: 200vw; /* Altura de la pantalla */
  width: 99vw; /* Ancho de la pantalla */
  background-color: #171421; /* Colocar el valor del color neutral-950 */
  background-image: radial-gradient(ellipse at 50% -20%, rgba(120, 119, 198, 0.3), rgba(255, 255, 255, 0));
}

